<template>
  <div>
    <bread-nav :nav="nav"></bread-nav>
    <main-part>
      <div class="btn-block">
        <div class="btn-main" @click="ClickDateLine">导出</div>
      </div>
      <search-box :searchData="searchData" :table="table"></search-box>
      <resize-table>
        <table class="table">
          <thead>
          <tr>
            <th width="12%">班级</th>
            <th width="10%">姓名</th>
            <th width="12%">早自习到岗</th>
            <th width="12%">课间操跟跑</th>
            <th width="12%">晚自习到岗</th>
            <th width="12%">班主任例会</th>
            <th width="10%">周常规</th>
            <th width="10%">月常规</th>
            <th width="10%">总分</th>
          </tr>
          </thead>
          <tbody>
          <tr v-for="(item,key) in table.list">
            <td>{{item.className}}</td>
            <td>{{item.teacherName}}</td>
            <td>{{item.morningLessonNum}}</td>
            <td>{{item.breakExerciseNum}}</td>
            <td>{{item.eveningLessonNum}}</td>
            <td>{{item.regularMeetingNum}}</td>
            <td>{{item.weekRoutineNum}}</td>
            <td>{{item.monthRoutineNum}}</td>
            <td>{{item.totalAssessNum}}</td>
          </tr>
          </tbody>
        </table>
      </resize-table>
      <no-data v-show="!table.noData"></no-data>
      <paging :data.sync="table"></paging>
    </main-part>
  </div>
</template>
<script>
  import mixins from '../../../../assets/js/mixins'

  export default {
    mixins: [mixins.$exportDetail],
    data () {
      return {
        nav: [{name: '班主任考评', path: ''}],
        searchData: [
          {
            type: 'date', title: '月份', dateType: 'month',
            model: 'month', initialDate: new Date(new Date().setMonth(new Date().getMonth()))
          },
          {type: 'select', title: '年级', model: 'gradeId', option: []},
        ],
        table: {
          url: '/advisorAssess/queryAdvisorAssessPage.do',
          el: '.table',
          total: 0,
          noData: true,
          list: [],
          query: {
            month: new Date(new Date().setMonth(new Date().getMonth())).$Format('yyyy-MM-dd hh:mm:ss'),
            gradeId: null,
            teacherPosition: 'HEADMASTER'
          }
        },
      }
    },
    methods: {
      // 处理时间--导出
      ClickDateLine () {
        let newDate = this.table.query.month
        this.table.query.month = this.table.query.month.substr(0, 10)
        this.table.query.month = this.table.query.month.replace(/(\d{4})-(\d{2})-(\d{2})/g, '$1/$2/$3')
        this.$exportDetail('/export/teacherAssessExport.do')
        this.table.query.month = newDate
      },
      // 获取年级列表
      async getGradeList () {
        let res = await this.$ajax({
          url: '/grade/queryGradeList.do',
        })
        this.searchData[1].option = []
        res.forEach(e => {
          this.searchData[1].option.push({
            label: e.name,
            value: e.id
          })
        })
        this.table.query.gradeId = res[0].id
      },
    },
    async mounted () {
      await this.getGradeList()
      this.$search(this.table)
    }
  }
</script>
<style lang="sass" scoped>
</style>
